    <readme
      text="–ë–µ—Å–ø–ª–∞—Ç–Ω—ã–π –æ–Ω–ª–∞–π–Ω –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä –ø–æ–º–æ–∂–µ—Ç —Ä–∞—Å—Å—á–∏—Ç–∞—Ç—å –æ—Ç–º–æ—Å—Ç–∫—É –≤–æ–∫—Ä—É–≥ –≤–∞—à–µ–≥–æ –¥–æ–º–∞. –° –µ–≥–æ –ø–æ–º–æ—â—å—é –≤—ã —É–∑–Ω–∞–µ—Ç–µ —Ç–æ—á–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –ø–æ–Ω–∞–¥–æ–±—è—Ç—Å—è –¥–ª—è —Å—Ç—Ä–æ–∏—Ç–µ–ª—å—Å—Ç–≤–∞. –í—Å–µ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ã —Ä–∞—Å—á–µ—Ç–æ–≤ –º–æ–∂–Ω–æ —Å–∫–∞—á–∞—Ç—å –Ω–∞ –≤–∞—à –∫–æ–º–ø—å—é—Ç–µ—Ä –∏–ª–∏ —Å–º–∞—Ä—Ç—Ñ–æ–Ω. –ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä –æ—Ç–º–æ—Å—Ç–∫–∏ –≤—ã—á–∏—Å–ª–∏—Ç, –∫–∞–∫–æ–π –æ–±—ä–µ–º –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤ –ø–æ–Ω–∞–¥–æ–±–∏—Ç—Å—è –¥–ª—è —Å—Ç—Ä–æ–∏—Ç–µ–ª—å—Å—Ç–≤–∞ —Å–ª–æ–∂–Ω–æ–π –ø—Ä—è–º–æ—É–≥–æ–ª—å–Ω–æ–π –∫–æ–Ω—Å—Ç—Ä—É–∫—Ü–∏–∏, —É—á–∏—Ç—ã–≤–∞—è –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ –∏ –≤–Ω–µ—à–Ω–∏–µ —É–≥–ª—ã –ø–æ –ø–µ—Ä–∏–º–µ—Ç—Ä—É. –¢–∞–∫–∂–µ –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞—Ö –≤—ã—á–∏—Å–ª–µ–Ω–∏–π –≤—ã —É–≤–∏–¥–∏—Ç–µ, —Å–∫–æ–ª—å–∫–æ –ø–æ—Ç—Ä–µ–±—É–µ—Ç—Å—è –±—Ä—É—Å—á–∞—Ç–∫–∏, —É—Ç–µ–ø–ª–∏—Ç–µ–ª—è, –≥–∏–¥—Ä–æ–∏–∑–æ–ª—è—Ü–∏–æ–Ω–Ω–æ–≥–æ –º–∞—Ç–µ—Ä–∏–∞–ª–∞, –≥–µ–æ—Ç–µ–∫—Å—Ç–∏–ª—è, –æ–ø–∞–ª—É–±–∫–∏, –±–æ—Ä–¥—é—Ä–∞, –∞—Ä–º–∞—Ç—É—Ä–Ω–æ–π —Å–µ—Ç–∫–∏, —â–µ–±–Ω—è, –ø–µ—Å–∫–∞ –∏ –±–µ—Ç–æ–Ω–∞. –ü–æ–∫–∞–∑–∞—Ç–µ–ª–∏ —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞—é—Ç—Å—è, –∏—Å—Ö–æ–¥—è –∏–∑ –ø–µ—Ä–∏–º–µ—Ç—Ä–∞, –ø–ª–æ—â–∞–¥–∏ –∏ –æ–±—ä–µ–º–∞. –û–±—Ä–∞—Ç–∏—Ç–µ –≤–Ω–∏–º–∞–Ω–∏–µ –Ω–∞ —Ä–µ–∫–æ–º–µ–Ω–¥—É–µ–º—ã–µ —Ä–∞–∑–º–µ—Ä—ã –æ—Ç–º–æ—Å—Ç–∫–∏ - –µ–µ —à–∏—Ä–∏–Ω–∞ –¥–æ–ª–∂–Ω–∞ —Å–æ—Å—Ç–∞–≤–ª—è—Ç—å –æ—Ç 80 –¥–æ 120 —Å–º, –∞ –Ω–∞ —É—á–∞—Å—Ç–∫–∞—Ö —Å–æ —Å–ª–æ–∂–Ω—ã–º–∏ –≥–µ–æ–ª–æ–≥–∏—á–µ—Å–∫–∏–º–∏ —É—Å–ª–æ–≤–∏—è–º–∏, –∫–∞–∫ –∫–∞—Ä—Å—Ç—ã - –æ—Ç 150 –¥–æ 300 —Å–º."
